//
// Beziers
//

$beziers:(
    bounce:#{0.750, -0.170, 0.230, 1.190},
    bouncein:#{0.715, 0.170, 0.145, 1.245},
    ease:#{0.875, 0.105, 0.190, 0.920},
    expo:#{1.000, 0.000, 0.000, 1.000},
    nice:#{0.625, 0.125, 0.305, 0.875},
    nicein:#{0.145, 0.310, 0.300, 0.870},
    sine:#{0.445, 0.050, 0.550, 0.950},
    out:#{0.660, 0.120, 0.985, 0.545}
);

//
//Shorthand map-get() for beziers to bezier(bezier-name)
//
@function bezier($curve) {
    @return map-get($beziers, $curve);
}
